header {
  padding: 0 1rem;
  display: flex;
  justify-content: left;
  align-items: center;
  text-transform: uppercase;
  border-bottom: solid 1px rgba(160,160,160,0.3);
  background-color: #fff;
}

.nav-item {
  text-decoration: none;
  color: inherit;
}

.index-link{
  color: burlywood;
  margin: 0.3rem 0;
}

.links{
  ul {
    list-style: none;
    display: flex;

    li{
      margin: 0 2rem;
    }
  }
}