.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 90%;
  padding: 5%;
  
  &_info{
    
    &-img{
      border-radius: 50%;
      width: 200px;
    }
    
    h1 {
      text-transform: uppercase;
    }

    a {
      color: burlywood;
      text-decoration: none;
    }
  }

  &_about {
    margin-top: 2rem;
    border-top: solid 1px rgba(160, 160, 160, 0.3);
    text-align: left;
  }
  
}